@use "../tools/variables" as *;
@use "../tools/mixins" as *;

.drinks {
  &_intro {
    @include setFlex(column, center, center, 40px);

    img {
      max-width: 300px;
      outline: 2px solid $ivory;
    }
  }

  .menu {
    @include setFlex(column, center, center, 40px);

    .beer {
      @include setFlex(column, null, null, 10px);
      min-width: 300px;
      width: 100%;
      max-width: 500px;
      font-family: $fontTexts;
      font-weight: 500;

      .spec-h2 {
        margin-bottom: 20px;
      }

      &_name {
        font-size: 1.4rem;
      }

      &_description {
        font-size: 1.2rem;
        font-style: italic;
        margin-left: 20px;
      }

      &_container {
        @include setFlex(row, space-between, center, 0);
        margin-left: 40px;
        font-size: 1.2rem;
        white-space: nowrap;
      }
      .underline {
        align-self: flex-end;
        margin-bottom: 5px;
        width: 60%;
        border-bottom: 1px dashed $brown;
      }
    }

    &_separator {
      margin: 40px auto 0;
      width: 100%;
      height: 3px;
      background-color: $brown;
    }
  }
}
